'use client';

import { useState } from 'react';
import { addWeeks, subWeeks } from 'date-fns';
import { WeekView } from './WeekView';
import { TaskForm } from './TaskForm';
import { useTasks } from '@/hooks/useTasks';
import { Task } from '@/lib/types';
import { motion, AnimatePresence } from 'framer-motion';

export const Calendar: React.FC = () => {
  const [currentDate, setCurrentDate] = useState(new Date());
  const [showTaskForm, setShowTaskForm] = useState(false);
  const [draggedTask, setDraggedTask] = useState<Task | null>(null);
  
  const { tasks, addTask, moveTask, loading } = useTasks();

  const nextWeek = () => setCurrentDate(addWeeks(currentDate, 1));
  const prevWeek = () => setCurrentDate(subWeeks(currentDate, 1));

  const handleAddTask = async (taskData: Omit<Task, 'id'>) => {
    await addTask(taskData);
    setShowTaskForm(false);
  };

  const handleTaskDragStart = (task: Task) => {
    setDraggedTask(task);
  };

  const handleTaskDragEnd = () => {
    setDraggedTask(null);
  };

  const handleDrop = async (date: Date) => {
    if (draggedTask) {
      await moveTask(draggedTask.id, date, new Date(date.getTime() + 60 * 60 * 1000));
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="text-lg">Loading...</div>
      </div>
    );
  }

  return (
    <div className="p-4 space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold">Task Scheduler</h1>
        <div className="flex gap-4 items-center">
          <div className="flex gap-2">
            <button
              onClick={prevWeek}
              className="px-4 py-2 bg-gray-100 rounded-md hover:bg-gray-200"
            >
              Previous Week
            </button>
            <button
              onClick={nextWeek}
              className="px-4 py-2 bg-gray-100 rounded-md hover:bg-gray-200"
            >
              Next Week
            </button>
          </div>
          <button
            onClick={() => setShowTaskForm(true)}
            className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700"
          >
            Add Task
          </button>
        </div>
      </div>

      {/* Task Form Modal */}
      <AnimatePresence>
        {showTaskForm && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50"
            onClick={() => setShowTaskForm(false)}
          >
            <motion.div
              initial={{ scale: 0.9, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.9, opacity: 0 }}
              onClick={(e) => e.stopPropagation()}
            >
              <TaskForm
                onSubmit={handleAddTask}
                onCancel={() => setShowTaskForm(false)}
              />
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Calendar Grid */}
      <div className="bg-white rounded-lg shadow">
        <WeekView
          tasks={tasks}
          currentDate={currentDate}
          onTaskDragStart={handleTaskDragStart}
          onTaskDragEnd={handleTaskDragEnd}
          onDrop={handleDrop}
        />
      </div>

      {/* Legend */}
      <div className="flex gap-4 text-sm">
        <div className="flex items-center gap-2">
          <div className="w-4 h-4 bg-blue-200 border-l-4 border-blue-500"></div>
          <span>Pending</span>
        </div>
        <div className="flex items-center gap-2">
          <div className="w-4 h-4 bg-green-200 border-l-4 border-green-500"></div>
          <span>Completed</span>
        </div>
        <div className="flex items-center gap-2">
          <div className="w-4 h-4 bg-red-200 border-l-4 border-red-500"></div>
          <span>Overdue</span>
        </div>
      </div>
    </div>
  );
};